                {title:'压缩图片tinypng',url:'/iframes',iframeUrl:'https://tinypng.com/'},
                {title:'JQuery插件库',url:'/iframes',iframeUrl:'http://www.jq22.com/'},
                {title:'PHP中文学习网',url:'/iframes',iframeUrl:'https://m.php.cn/'},
                {title:'掘金',url:'/iframes',iframeUrl:'https://juejin.im/'},
            ]
        }
    },
    mounted(){

    },
    methods:{}
}
</script>


